import { Country } from "country-state-city";
import { Col, Container, Form, Modal, Row } from "react-bootstrap";
import * as React from "react";
import { useStaticQuery, graphql } from 'gatsby';
import { initializeApp } from 'firebase/app';
import { collection, addDoc, getFirestore } from "@firebase/firestore";
import { useState } from 'react';

function TalkToUs(props) {

    const data = useStaticQuery(graphql`
    query {
      site(siteMetadata: {}) {
        id
        siteMetadata {
          firestore {
            apiKey
            authDomain
            projectId
            storageBucket
            messagingSenderId
            appId
          }
        }
      }
    }
  `);
    // Initialize Firebase
    const app = initializeApp({ ...data.site.siteMetadata.firestore });
    const db = getFirestore(app);
    
    const { handleClose, show } = props;
    const countries = Country.getAllCountries();
    const init = {
        name: "",
        email: "",
        country: "",
        contact: "",
        message: ""
    };
    const [talkToUsInp, setTalkToUsInp] = useState(init);


    const talkToUs = async (e) => {
        e.preventDefault();
        const clients = collection(db, 'clients');
        await addDoc(clients, talkToUsInp);
        handleClose();
        setTalkToUsInp(init);
    };

    function handleTalkToUsInpChange(event) {
        const { name, value } = event.target;
        setTalkToUsInp(prevFormData => ({ ...prevFormData, [name]: value }));
    }

    return (
        <>
            <Container>
                <Modal backdrop='static' keyboard={false} size="lg" centered show={show} onHide={handleClose}>
                    <Modal.Header>
                        <Modal.Title>
                            <button onClick={handleClose} className="close"><i className="bi bi-x"></i></button>
                            Talk to us
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Row>
                                <Col sm={12} lg={6}>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Label>Name</Form.Label>
                                        </Col>
                                        <Col md={12}>
                                            <Form.Control type="text" required name="name" value={talkToUsInp.name}
                                                onChange={handleTalkToUsInpChange} />
                                        </Col>
                                    </Row>
                                </Col>
                                <Col sm={12} lg={6}>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Label>Email</Form.Label>
                                        </Col>
                                        <Col md={12}>
                                            <Form.Control type="email" required name="email" value={talkToUsInp.email}
                                                onChange={handleTalkToUsInpChange} />
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                            <Row>
                                <Col sm={12} lg={6}>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Label>Country</Form.Label>
                                        </Col>
                                        <Col md={12}>
                                            <Form.Select required name="country" onChange={handleTalkToUsInpChange}>
                                                <option value={""}>Select</option>
                                                {countries.map((ele, ind) => (
                                                    <option key={ind} value={ele.isoCode}>{ele.name}</option>
                                                ))}
                                            </Form.Select>
                                        </Col>
                                    </Row>
                                </Col>
                                <Col sm={12} lg={6}>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Label>Skype / whatsapp / mobile</Form.Label>
                                        </Col>
                                        <Col md={12}>
                                            <Form.Control type="tel" required name="contact" value={talkToUsInp.contact}
                                                onChange={handleTalkToUsInpChange} />
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={12}>
                                    <Form.Label>Content</Form.Label>
                                </Col>
                                <Col md={12}>
                                    <Form.Control rows={'5'} as={'textarea'} required value={talkToUsInp.message}
                                        onChange={handleTalkToUsInpChange} name="message" />
                                </Col>
                            </Row>
                            <button type="submit" onClick={talkToUs} className="contact-us-submit-btn">
                                Submit
                            </button>
                        </Form>
                    </Modal.Body>
                </Modal>
            </Container>
        </>
    )
};

export default TalkToUs;